@import url(https://fonts.googleapis.com/css2?family=Volkhov&display=swap);
#simple-arrow-button {
    background-color: rgba(255, 255, 255, 0.2);
    color: white;
    border-style: none;
    border-radius: 15px;
    box-shadow: 0px, 0px, 2px, 4px;
}
#filled-circle {
    display: flex;
    position: relative;
    width: 320px;
    height: 320px;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    color: white;
    font-family: "Volkhov";
}

#shadow {
    position: relative;
    width: 200px;
    height: 50px;
    margin-top: 20px;
    background-color: rgb(12, 12, 53);
    border-radius: 230%;
}

.anim-filled-circle-bouncing {
    animation-name: bouncing;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.anim-shadow-soft-spreading {
    animation: shadow-transform 1s linear infinite;
}

.tech-circle {
    display: flex;
    width: 350px;
    position: relative;
    top: 30px;
    flex-direction: column;
    align-items: center;
}

.soft-shader {
    background: linear-gradient(blue, rgb(6, 6, 41));
}

.soft-shadow {
    box-shadow:
        black 0px 40px 4px 1px;
}

@keyframes bouncing {
    0% {
        top: 0px;
    }

    /* 
    25% {
        top:5px
    } */

    50% {
        top: 10px;
    }

    85% {
        top: 5px;
    }

    100% {
        top: 0px;
    }
}

@keyframes shadow-transform {

    0% {
        width: 208px;
    }

    50% {
        width: 200px;
    }

    85% {
        width: 205px;
    }

    100% {
        width: 208px;
    }

}
.container {
    background-color: rgba(28, 160, 255, 0.25);
    border-radius: 10px;
    box-shadow: 0px, -4px, 4px, 4px;
    width: 400px;
    height: 650px;
}

#card-title {
    font-size: x-large;
    text-align: center;
    padding: 15px;
}

#card-subtitle {
    padding: 10px;
}

#card-title, #card-subtitle {
    color: white;
    font-family: 'Volkhov';
}

#card-title::first-letter, #card-subtitle::first-letter {
    text-transform: uppercase;
}

.w-75 {
    width: 75%;
}

.w-100 {
    width: 100%;
}

.h-75 {
    height: 75%;
}

.h-100 {
    height: 100%;
}
:root {
    --default-height: 450px;
    --device-default-color: rgb(29, 24, 24);
}

#device-screen {
    display: block;
    width: 95%;
    height: 90%;
    margin: auto;
}

.desktop, .mobile, .tablet {
    border-radius: 10px;
    display: flex;
    height: 450px;
    height: var(--default-height);
    background-color: rgb(29, 24, 24);
    background-color: var(--device-default-color);
}

.desktop {
    width: 500px;
    height: 300px;
}

.mobile {
    width: 250px;
}

.tablet {
    width: 350px;
}

/*TODO fix dimensions to fill background with fullscreen image*/
.screen-img-dimensions {
    width: inherit;
    height: inherit;
    margin: auto;
}

.screen-img-fill {
    background-image: url("https://bit.ly/3iPv7NC");
    background-position: center;
}
:root {
    --primary-darkblue: #06154A;
    --secondary-blue: #1D3384;
    --terciary-skyblue: #1CA0FF;
    --primary-gray: #72818b;
    --tipografy-style: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

#domain-name::after {
    content: ".dev";
    color: #1CA0FF;
    color: var(--terciary-skyblue);
}

#profile-name {
    font-size: medium;
}

#profile-container {
    margin: auto;
    display: block;
}

#profile-name, .navbar {
    text-align: center;
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-family: var(--tipografy-style);
    font-weight: bold;
    font-size: medium;
}

#card-body {
    padding-inline: 20px;
    height: 70%;
}

#rotating-slider {
    display: inline-flex;
    height: 100%;
    width: 800px;
}

#device-presentation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    width: 90%;
}

.header {
    background: url(/static/media/cover-picture.d2066f28.jpg);
    background-size: cover;
    background-position: center;
    display: flex;
    height: 450px;
}

.navbar {
    position: fixed;
    width: 100%;
    display: inline-flex;
    justify-content: space-between;
    background-color: rgba(128, 128, 128, 0.6);
    top: 0px;
}

.navbar div {
    display: flex;
    height: inherit;
}

.nav-list {
    display: inline-flex;
    width: 500px;
    margin-block: auto;
    justify-content: space-around;
}

.profile-picture, .icon {
    border-radius: 100%;
}

.slides-control-button {
    display: flex;
    justify-content: center;
    align-self: center;
    align-items: flex-end;
    margin-bottom: 30px;
    height: 15%;
}

.desc-picture {
    border-radius: 10px;
    height: 350px;
    width: 280px;
}

.text-smaller {
    color: white;
}

.text-bigger {
    color: white;
    font-size: larger;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-family: var(--tipografy-style);
}

.piece-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #06154A;
    background-color: var(--primary-darkblue);
    height: 600px;
}

.piece-container-secondary {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.portfolio {
    align-self: center;
    width: 1300px;
}

.right-section {
    justify-content: right;
}

.skills {
    display: inline-flex;
    justify-content: space-between;
    width: 100%;
}

.technologies {
    height: 800px;
}


.contact-card {
    display: block;
    border-style: solid;
    border-width: 2px;
    border-radius: 10px;
    border-color: #1D3384;
    border-color: var(--secondary-blue);
    -webkit-column-count: 2;
            column-count: 2;
    height: 200px;
    width: 450px;
    margin-bottom: 20px;
}

main {
    margin-inline: 5px;
    height: 2000px;
}


h5 {
    color: white;
}
:root {
    --primary-darkblue: #06154A;
    --secondary-blue: #1D3384;
}

body {
    background-color: #1D3384;
    background-color: var(--secondary-blue);
}
